	import {  
		mapState,
        mapMutations  
    } from 'vuex';
	
	export default{
		
		data(){
			return {
				loginName: '',
				password: '',
				loginPassword: '',
				resultData:{},
				result: '',
				logining: false
			}
		},
		onLoad(){
			console.log('login页面onLoad');
		},
		computed: {
			...mapState(['hasLogin','uerInfo'])
		},
		methods: {
			...mapMutations(['login']),
			inputChange(e){
				const key = e.currentTarget.dataset.key;
				this[key] = e.detail.value;
			},
			navBack(){
				uni.navigateBack({
					delta: 1
				});
			},
			toRegist(){
				this.$api.msg('去注册');
				uni.navigateTo({
					url: '/pages/public/register',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			},
			toLoginByPhone(){
				uni.navigateTo({
					url: '/pages/public/loginByPhone',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			},
			toForgetPwd(){
				uni.navigateTo({
					url: '/pages/public/forgetPwd',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			},
			async toLogin(){
				this.logining = true;
				let loginPhone = this.loginPhone;
				let loginPassword = this.loginPassword;
				const {loginName, password} = this;
				
				uni.request({
				   // url:'http://localhost:8888/api/getAll',
				   url: this.apiServer+'/customer/loginByName',
				   method: 'POST',
				   dataType: "json",
				   data: { 
					   "loginName": this.loginName,
				   },
				    success: (res) => {
						this.result = res.data
						console.log(this.result)
						if(this.result.length == 0){
							this.logining = false;
							this.$api.msg("用户名或密码错误");
						}else{
							 if(this.result[0].pwd == this.password){
								 this.login(this.result[0]);
								 uni.reLaunch({
								 	url: "/pages/user/user",
								 	success: res => {}
								 });
							 }
						}
				    },
					fail: () => {
						this.logining = false;
						this.$api.msg("网络错误");
					}
				});
				
			}
		},
	}
